import React, { useState, useEffect } from 'react';
import { ActivityIndicator, Text, View, Image, Button, TouchableOpacity, FlatList, StatusBar } from 'react-native';
import data from './assets/cards.json';
import SmallClassImage from './SmallClassImage';
import SoundButton from './SoundButton';
import cardsets from './assets/cardsets.json';
import { Audio } from 'expo-av';
import { NavigationContainer, useScrollToTop, useIsFocused } from '@react-navigation/native';
import FlatListItemSeparator from './FlatListItemSeparator';
import { createStackNavigator } from '@react-navigation/stack';
import { createBottomTabNavigator } from '@react-navigation/bottom-tabs';
import { NavigationEvents } from "react-navigation";
import { Toolbar } from 'react-native-material-ui';

function FocusAwareStatusBar(props) {
  const isFocused = useIsFocused();

  return isFocused ? <StatusBar {...props} /> : null;
}

const CardListTab = ( { route, navigation, state }) => {

  React.useLayoutEffect( () => {
    navigation.setOptions({headerShown: false});
  }, [navigation]);

  const flatListRef = React.useRef()
  useScrollToTop(flatListRef)

  const {cardSet} = route.params
  const {typeFilter} = route.params



  const myData = data
    .filter(x => (x.collectible === true && x.type === typeFilter) && (x.set === cardSet || cardSet === null))
    .sort(function(a,b) {
      var classA = a.cardClass
      var classB = b.cardClass

      if(classA < classB ) { return -1}
      if(classA > classB ) { return 1}
      if(classA < classB ) { return 0}
    })
    .slice(0,100)

    const [cardDisplayed, setCardDisplayed] = useState('none')
    const [cardID, setCardID] = useState('AT_009')
    const [myCardData, setMyCardData] = useState(myData)




function search(criteria) {
  setMyCardData(myData.filter(x => (x.name.toUpperCase()).includes(criteria.toUpperCase())))
}

  navigation.addListener('tabPress', e => {
    setMyCardData(myData)
    navigation.navigate('Cards', {
        cardSet: null
    })

  })

  navigation.addListener('focus', e => {
    goToTop()
    setMyCardData(myData)
  })

  function goToTop() {
    console.log('GoToTop')
    flatListRef.current.scrollToOffset({ animated: true, offset: 0 })
  }

  React.useLayoutEffect(() => {
     navigation.setOptions({
      headerRight: () => (
        <Button title='Test' onPress={() => setCount(c => c + 1)} title="Update count" />
      ),
    });
  }, [navigation]);

  return (
      <>
    <Toolbar
          onPress= {(e) => {
                        if (cardDisplayed == 'none') {
                          setCardDisplayed('')
                        } else if (cardDisplayed == '') {
                          setCardDisplayed('none')
                        }
                      }}
          centerElement="HearthSFX"
          searchable={{
            autoFocus: true,
            placeholder: 'Search',
            //onChangeText: (criteria) => { search(criteria) }
          }}
        />

          <View style={{ flex: 1, padding: 8, flexDirection: 'row'}}>
            <FlatList
              data={myCardData}
              renderItem={({ item }) => (
                <TouchableOpacity onPress= {() => {
                              setCardID(item.id)
                              if (cardDisplayed == 'none') {
                                setCardDisplayed('')
                              }
                            }}>
                  <View style={{ flex: 1, flexDirection: 'row', alignItems: 'center'
                  , justifyContent: 'space-between'
                  , marginTop: 4, marginBottom: 4, marginRight: 8}}>
                    <SmallClassImage cardClass={item.cardClass}/>
                    <Text style={{flexGrow: 1}} >{item.name}</Text>
                    <SoundButton title='Play' cardID={item.id} sound='P' />
                    <View style={{padding: 4}} />
                    <SoundButton title='Attack' cardID={item.id} sound='A' />
                    <View style={{padding: 4}} />
                    <SoundButton title='Death' cardID={item.id} sound='D' />
                  </View>
                  </TouchableOpacity>

              )}
              ItemSeparatorComponent={FlatListItemSeparator}
              ref={flatListRef}
            />

            <View style={{justifyContent:'center', display:cardDisplayed}}>
               <Card cardID={cardID}/>
            </View>
          </View>
          </>
        )
}

const Card = (props) => {
  return (
    <Image source={{uri: 'https://art.hearthstonejson.com/v1/render/latest/enUS/256x/'+props.cardID+'.png'}}
      style={{width: 256, height: 388}}/>
  )
}

export default CardListTab;
